import React, { useEffect, useMemo, useState } from 'react';
import classNames from 'classnames';
import { useDebounce } from 'use-debounce';
import {
  Button, Container, Divider, Form, Grid, Header, Input, Label, Loader,
} from 'semantic-ui-react';

import withAuth from './with-auth';
import useField from './use-field';

import styles from './App.module.css';

import 'semantic-ui-css/semantic.min.css';

const DICTIONARY_TYPES = [
  ['sanstv', 'sanstv.ru'],
  ['sociation', 'sociation.org'],
];

const isAnagram = (word1, word2) => {
  if (word1.length !== word2.length) {
    return false;
  }

  const word1Sorted = word1.split('').sort().join('');
  const word2Sorted = word2.split('').sort().join('');

  return word1Sorted === word2Sorted;
}

const isPlusogram = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  const word1Sorted = word1.split('').sort().join('');
  const word2Sorted = word2.split('').sort().join('');

  let hasDifference = false;
  for (let i = 0; i < word2Sorted.length; i++) {
    if (word1Sorted.charAt(i - +hasDifference) !== word2Sorted.charAt(i)) {
      if (hasDifference) {
        return false;
      }

      hasDifference = true;
    }
  }

  return hasDifference;
}

const isMetagram = (word1, word2) => {
  if (word1.length !== word2.length) {
    return false;
  }

  let hasDifference = false;
  for (let i = 0; i < word1.length; i++) {
    if (word1.charAt(i) !== word2.charAt(i)) {
      if (hasDifference) {
        return false;
      }

      hasDifference = true;
    }
  }

  return hasDifference;
}

const isMetanagram = (word1, word2) => {
  if (word1.length !== word2.length) {
    return false;
  }

  for (let i = 0; i < word1.length; i++) {
    word2 = word2.replace(word1.charAt(i), '');
  }

  return word2.length === 1;
}

const isLogogrif = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  let hasDifference = false;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +hasDifference) !== word2.charAt(i)) {
      if (hasDifference) {
        return false;
      }

      hasDifference = true;
    }
  }

  return hasDifference;
}

const isLogogrif2 = (word1, word2) => {
  if (word2.length - word1.length !== 2) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - totalDifferences) === word2.charAt(i)) {
      if (totalDifferences === 1) {
        return false;
      }
    } else {
      if (totalDifferences === 2) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 2;
}

const isMetagrif = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +!!totalDifferences) !== word2.charAt(i)) {
      if (totalDifferences === 2) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 2;
}

const isBrukva = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +!!totalDifferences) === word2.charAt(i)) {
      if (totalDifferences === 1) {
        return false;
      }
    } else {
      if (totalDifferences === 2) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 2;
}

const createSharada = (word1, word2, dictionarySet) => {
  const word = word1 + word2;
  if (dictionarySet.has(word)) {
    return word;
  }

  return null;
}

const getFormats = (word, dictionary) => {
  const anagrams = [];
  const plusograms = [];
  const metagrams = [];
  const metanagrams = [];
  const logogrifs = [];
  const logogrifs2 = [];
  const metagrifs = [];
  const brukvas = [];
  const sharadas = [];

  if (word.length > 0) {
    dictionary.array.forEach((wordDictionary) => {
      if (wordDictionary === word) {
        return;
      }

      if (isAnagram(word, wordDictionary)) {
        anagrams.push(wordDictionary);
      }

      if (isPlusogram(word, wordDictionary) || isPlusogram(wordDictionary, word)) {
        plusograms.push(wordDictionary);
      }

      if (isMetagram(word, wordDictionary)) {
        metagrams.push(wordDictionary);
      }

      if (isMetanagram(word, wordDictionary) || isMetanagram(wordDictionary, word)) {
        metanagrams.push(wordDictionary);
      }

      if (isLogogrif(word, wordDictionary) || isLogogrif(wordDictionary, word)) {
        logogrifs.push(wordDictionary);
      }

      if (isLogogrif2(word, wordDictionary) || isLogogrif2(wordDictionary, word)) {
        logogrifs2.push(wordDictionary);
      }

      if (isMetagrif(word, wordDictionary) || isMetagrif(wordDictionary, word)) {
        metagrifs.push(wordDictionary);
      }

      if (isBrukva(word, wordDictionary) || isBrukva(wordDictionary, word)) {
        brukvas.push(wordDictionary);
      }

      let sharada = createSharada(word, wordDictionary, dictionary.set);
      if (sharada) {
        sharadas.push(sharada);
      }

      sharada = createSharada(wordDictionary, word, dictionary.set);
      if (sharada) {
        sharadas.push(sharada);
      }
    });
  }

  return {
    anagrams,
    plusograms,
    metagrams,
    metanagrams,
    logogrifs,
    logogrifs2,
    metagrifs,
    brukvas,
    sharadas,
  };
}

const App = () => {
  const [dictionary, setDictionary] = useState(null);
  const [word, handleWordChange] = useField('');
  const [dictionaryType, handleDictionaryTypeChange] = useField('sanstv');
  const [wordDebounced] = useDebounce(word, 500);

  const wordClean = useMemo(() => wordDebounced.trim().toLowerCase().replace('ё', 'е'), [
    wordDebounced,
  ]);

  const {
    anagrams,
    plusograms,
    metagrams,
    metanagrams,
    logogrifs,
    logogrifs2,
    metagrifs,
    brukvas,
    sharadas,
  } = useMemo(() => {
    if (!dictionary) {
      return {};
    }

    return getFormats(wordClean, dictionary[dictionaryType]);
  }, [dictionary, dictionaryType, wordClean]);

  useEffect(() => {
    (async () => {
      const { sanstv, sociation } = await import('./dictionary.json');

      setDictionary({
        sanstv: {
          array: sanstv,
          set: new Set(sanstv),
        },
        sociation: {
          array: sociation,
          set: new Set(sociation),
        },
      });
    })();
  }, []);

  if (!dictionary) {
    return <Loader active>Загружаю базу...</Loader>;
  }

  return (
    <Container>
      <Grid padded>
        <Grid.Row>
          <Grid.Column>
            <Form>
              <Input
                fluid
                autoFocus
                value={word}
                placeholder="Слово"
                action={
                  <Button.Group size="big" className={styles.buttons}>
                    {DICTIONARY_TYPES.map(([value, content]) => (
                      <Button
                        key={value}
                        value={value}
                        content={content}
                        active={value === dictionaryType}
                        onClick={handleDictionaryTypeChange}
                      />
                    ))}
                  </Button.Group>
                }
                size="big"
                onChange={handleWordChange}
              />
            </Form>
          </Grid.Column>
        </Grid.Row>
        {anagrams.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Анаграммы</Header>
              </Divider>
              <div className={styles.words}>
                {anagrams.map((anagram, i) => (
                  <Label
                    horizontal
                    key={anagram}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {anagram}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {plusograms.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Плюсограммы</Header>
              </Divider>
              <div className={styles.words}>
                {plusograms.map((plusogram, i) => (
                  <Label
                    horizontal
                    key={plusogram}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {plusogram}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {metagrams.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Метаграммы</Header>
              </Divider>
              <div className={styles.words}>
                {metagrams.map((metagram, i) => (
                  <Label
                    horizontal
                    key={metagram}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {metagram}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {metanagrams.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Метанаграммы</Header>
              </Divider>
              <div className={styles.words}>
                {metanagrams.map((metanagram, i) => (
                  <Label
                    horizontal
                    key={metanagram}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {metanagram}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {logogrifs.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Логогрифы</Header>
              </Divider>
              <div className={styles.words}>
                {logogrifs.map((logogrif, i) => (
                  <Label
                    horizontal
                    key={logogrif}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {logogrif}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {logogrifs2.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Логогрифы +2</Header>
              </Divider>
              <div className={styles.words}>
                {logogrifs2.map((logogrif2, i) => (
                  <Label
                    horizontal
                    key={logogrif2}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {logogrif2}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {metagrifs.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Метагрифы</Header>
              </Divider>
              <div className={styles.words}>
                {metagrifs.map((metagrif, i) => (
                  <Label
                    horizontal
                    key={metagrif}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {metagrif}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {brukvas.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Брюквы</Header>
              </Divider>
              <div className={styles.words}>
                {brukvas.map((brukva, i) => (
                  <Label
                    horizontal
                    key={brukva}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {brukva}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {sharadas.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Шарады</Header>
              </Divider>
              <div className={styles.words}>
                {sharadas.map((sharada, i) => (
                  <Label
                    horizontal
                    key={sharada}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {sharada}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
      </Grid>
    </Container>
  );
};

export default withAuth(App);
